

export class CarouselStore {
    type: string;
    deviceType?: string;
    classText: string;
    items: number;
    load: number;
    deviceWidth: number;
    carouselWidth: number;
    width: number;
    pointNumbers: number;
    visibleItems: number;
    slideItems: number;
    itemWidthPer: number;
    transform: Transfrom;
    loop: boolean;
    dexVal: number;
    touchTransform: number;
    touch: Touch;
    isEnd: boolean;
  }
  
  export class Touch {
    swipe: string;
    velocity: number;
  }
  
  export class Transfrom {
    xs: number;
    sm: number;
    md: number;
    lg: number;
    all: number;
  }
  
  export class Carousel {
    grid: Transfrom;
    slide?: number;
    speed?: number;
    interval?: number;
    animation?: string;
    point?: boolean;
    load?: number;
    custom?: string;
    loop?: boolean;
    dynamicLength: boolean;
  }
  